.offcanvas-trigger {
  position: relative;
  width: 36px;
  height: 36px;
  display: inline-block;
  padding: 0;
  border: 1px solid transparent;
  background-color: transparent;
  transition: opacity 0.3s ease, background-color 0.3s ease;
}
.offcanvas-trigger span {
  display: block;
  position: absolute;
  height: 2px;
  width: 24px;
  background: #00233D;
  opacity: 1;
  transform: rotate(0deg);
  transition: all 0.25s ease-in-out;
  transform-origin: left center;
}
.offcanvas-trigger span:nth-child(1) {
  transform: rotate(45deg);
  left: 8px;
  top: 7px;
}
.offcanvas-trigger span:nth-child(2) {
  opacity: 0;
  width: 0;
  left: 18px;
  top: 16px;
}
.offcanvas-trigger span:nth-child(3) {
  transform: rotate(-45deg);
  left: 8px;
  top: 24px;
}
.offcanvas-trigger.closed span:nth-child(1) {
  transform: rotate(0);
  left: 5px;
  top: 8px;
}
.offcanvas-trigger.closed span:nth-child(2) {
  opacity: 1;
  width: 24px;
  left: 5px;
  top: 16px;
}
.offcanvas-trigger.closed span:nth-child(3) {
  transform: rotate(0);
  left: 5px;
  top: 24px;
}

